.cart {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background: #e5e5e5;
  margin-right: 20px;
  z-index: 1000;
  &__main {
    justify-content: space-between;
    box-sizing: border-box;
    padding: 24px 23px;
    background: #ffffff;
    max-height: 100vh;
    max-width: 636px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    height: 100%;
  }
  &__header {
    padding-bottom: 20px;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    align-items: center;
  }
  &__body {
    display: flex;
    flex-direction: column;
    gap: 20px;
    flex: 1 1 auto;
    padding: 18px 0;
    overflow: auto;
    margin-bottom: 20px;

    & {
      scrollbar-width: thin;
      scrollbar-color: #d8d9cc #d8d9cc;
    }
    &::-webkit-scrollbar {
      width: 5px;
    }
    &::-webkit-scrollbar-thumb {
      background-color: #d8d9cc;
      border-radius: 20px;
    }
  }

  &__title {
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
  }
  &__icon {
  }

  &__limit {
    width: 100px;
    box-sizing: border-box;
    border: 0.5px solid #d8d9cc;
    border-radius: 20px;
    padding: 12px 32px;
    height: 40px;
    background: #ffffff;
    outline: 0;
    outline-offset: 0;
    &:active,
    &:hover,
    &:focus {
      border: 0.5px solid #bf9180;
    }
  }
}

.close {
  width: 20px;
  cursor: pointer;
  position: relative;
  &__first,
  &__second {
    top: 50%;
    position: absolute;
    display: block;
    height: 2px;
    width: 20px;
    background: #000;
  }
  &__first {
    transform: rotate(45deg);
  }
  &__second {
    transform: rotate(-45deg);
  }
  &:hover {
    transform: scale(1.1);
  }
}

.page {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 0.5px solid #d8d9cc;
  border-radius: 20px;
  &__prev {
    display: flex;
    align-items: center;
    padding: 0 15px;
    cursor: pointer;
    height: 100%;
    border-right: 0.5px solid #d8d9cc;
  }
  &__value {
    padding: 0 10px;
  }
  &__next {
    border-left: 0.5px solid #d8d9cc;
    display: flex;
    align-items: center;
    padding: 0 15px;
    cursor: pointer;
    height: 100%;
  }
}

